import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.scss']
})
export class PostListComponent implements OnInit {
@Input() title;
  @Input() content;
  @Input() loveIts;
  @Input() created_at
  constructor() { }

  ngOnInit() {
  }
  onLoveIt(){
    this.loveIts++
  }
  onDontLoveIt(){
    this.loveIts--
  }
  isLoved(){
    return this.loveIts > 0
  }
  isUnLoved(){
    return this.loveIts < 0
  }
getColor(){
    if(this.isLoved()) return 'green'
      else if(this.isUnLoved()) return 'red'
        else return 'black'
}
}


